'use strict';

// MODULES //

import shape2strides from '@stdlib/ndarray-base-shape2strides';
import strides2offset from '@stdlib/ndarray-base-strides2offset';
import resolveStr from '@stdlib/ndarray-base-dtype-resolve-str';
import numel from '@stdlib/ndarray-base-numel';
import getDType from '@stdlib/ndarray-base-dtype';
import getShape from '@stdlib/ndarray-base-shape';
import getOrder from '@stdlib/ndarray-base-order';
import emptyArray from '@stdlib/array-empty';
import allocUnsafe from '@stdlib/buffer-alloc-unsafe';


// MAIN //

/**
* Creates an uninitialized ndarray having the same shape and data type as a provided ndarray.
*
* @param {ndarray} x - input array
* @throws {TypeError} first argument must have a recognized data type
* @returns {ndarray} ndarray
*
* @example
* import zeros from '@stdlib/ndarray-base-zeros';
* import getShape from '@stdlib/ndarray-shape';
* import getDType from '@stdlib/ndarray-dtype';
*
* var x = zeros( 'float32', [ 2, 2 ], 'row-major' );
* // returns <ndarray>
*
* var y = emptyLike( x );
* // returns <ndarray>
*
* var sh = getShape( y );
* // returns [ 2, 2 ]
*
* var dt = String( getDType( y ) );
* // returns 'float32'
*/
function emptyLike( x ) {
	var ndims;
	var len;
	var buf;
	var ord;
	var dts;
	var sh;
	var st;
	var dt;

	dt = getDType( x );
	dts = resolveStr( dt );

	sh = getShape( x, true );
	ord = getOrder( x );

	ndims = sh.length;
	if ( ndims > 0 ) {
		len = numel( sh );
		st = shape2strides( sh, ord );
	} else {
		// For 0-dimensional arrays, the buffer should contain a single element...
		len = 1;
		st = [ 0 ];
	}
	if ( dts === 'binary' ) {
		buf = allocUnsafe( len );
	} else {
		buf = emptyArray( len, dts );
	}
	// FIXME: add support for struct dtypes. Will need to do something similar to `array/empty` in so far as allocating an uninitialized array buffer, performing byte alignment, and creating a StructArray view atop the buffer.

	return new x.constructor( dt, buf, sh, st, strides2offset( sh, st ), ord );
}


// EXPORTS //

export default emptyLike;
